header.bm-header .dach .takeda-logo img {
  height: 25px;
  width: 75px;
}

.fa::before,
.fas::before,
.far::before,
.fal::before,
.fad::before,
.fab::before {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  display: inline-block;
  font-style: normal;
  font-variant: normal;
  text-rendering: auto;
  line-height: 1;
}

[class^=fa-]::before,
[class*=" fa-"]::before {
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
}

[class^="fas fa-"]::before,
[class*=" fas fa-"]::before {
  font-family: "fa-solid-900" !important;
}

.fa-inverse::before {
  color: #064a7a;
}

[class*=fa-].has-circle::before {
  background-color: white;
  border-radius: 100%;
  width: 2em;
  height: 2em;
  line-height: 2em;
  box-sizing: border-box;
}